import React, { useEffect, useState } from 'react';
import { onAuthStateChanged } from 'firebase/auth';
import { collection, addDoc, getDocs, doc, updateDoc, deleteDoc } from 'firebase/firestore';
import { auth, db } from './firebase'; // Import auth and db from your firebase config file
import ExpenseTable from './components/ExpenseTable';
import Auth from './components/Auth';
import { Button, Modal, Form, Row, Col } from 'react-bootstrap';
import './App.css';

const App = () => {
  const [expenses, setExpenses] = useState([]);
  const [deletedExpenses, setDeletedExpenses] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [currentExpense, setCurrentExpense] = useState({ Expense: '', Amount: '', Paid: '', Same: false });
  const [editingIndex, setEditingIndex] = useState(null);
  const [user, setUser] = useState(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      if (currentUser) {
        loadExpenses(currentUser.uid);
      } else {
        setExpenses([]);
      }
    });

    return () => unsubscribe();
  }, []);

  const loadExpenses = async (userId) => {
    const expensesCollection = collection(db, 'users', userId, 'expenses');
    const expensesSnapshot = await getDocs(expensesCollection);
    const expensesList = expensesSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    setExpenses(expensesList);
  };

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    const newValue = type === 'checkbox' ? checked : value;
    if (name === 'Same' && checked) {
      setCurrentExpense({ ...currentExpense, Same: checked, Paid: currentExpense.Amount });
    } else if (name === 'Same' && !checked) {
      setCurrentExpense({ ...currentExpense, Same: checked });
    } else {
      setCurrentExpense({ ...currentExpense, [name]: newValue });
    }
  };

  const handleSaveClick = async () => {
    if (editingIndex !== null) {
      await editExpense(editingIndex, currentExpense);
    } else {
      await addExpense(currentExpense);
    }
    setShowModal(false);
    setCurrentExpense({ Expense: '', Amount: '', Paid: '', Same: false });
    setEditingIndex(null);
  };

  const handleClose = () => {
    setShowModal(false);
    setCurrentExpense({ Expense: '', Amount: '', Paid: '', Same: false });
    setEditingIndex(null);
  };

  const addExpense = async (expense) => {
    const expenseWithDifference = { ...expense, Difference: expense.Amount - expense.Paid };
    const docRef = await addDoc(collection(db, 'users', user.uid, 'expenses'), expenseWithDifference);
    setExpenses([...expenses, { ...expenseWithDifference, id: docRef.id }]);
  };

  const editExpense = async (index, updatedExpense) => {
    const expenseDocRef = doc(db, 'users', user.uid, 'expenses', expenses[index].id);
    const paid = updatedExpense.Paid !== undefined ? updatedExpense.Paid : expenses[index].Paid;
    const difference = updatedExpense.Amount - paid;
    const updatedExpenseWithDifference = { ...updatedExpense, Paid: paid, Difference: difference };
    await updateDoc(expenseDocRef, updatedExpenseWithDifference);

    const updatedExpenses = expenses.map((expense, i) =>
      i === index ? { ...updatedExpenseWithDifference, id: expense.id } : expense
    );
    setExpenses(updatedExpenses);
  };

  const deleteExpense = async (index) => {
    const expenseDocRef = doc(db, 'users', user.uid, 'expenses', expenses[index].id);
    await deleteDoc(expenseDocRef);

    const expenseToDelete = expenses[index];
    const updatedExpenses = expenses.filter((_, i) => i !== index);
    setExpenses(updatedExpenses);
    setDeletedExpenses([...deletedExpenses, expenseToDelete]);
  };

  const restoreLastDeleted = async () => {
    if (deletedExpenses.length > 0) {
      const lastDeleted = deletedExpenses.pop();
      await addExpense(lastDeleted);
      setDeletedExpenses([...deletedExpenses]);
    }
  };

  const toggleSame = async (index) => {
    const updatedExpense = { ...expenses[index], Same: !expenses[index].Same };
    await editExpense(index, updatedExpense);
  };

  const handleAddClick = () => {
    setShowModal(true);
  };

  const handleEditClick = (index) => {
    setEditingIndex(index);
    setCurrentExpense(expenses[index]);
    setShowModal(true);
  };

  const handleLogout = () => {
    auth.signOut();
  };

  if (!user) {
    return <Auth />;
  }

  // Inside the return statement of your App component
return (
  <div className="App">
    <h1>Welcome, {user.email}</h1>
    <div className="button-group">
      <Button variant="danger" onClick={handleLogout}>Logout</Button>
      <Button variant="primary" onClick={handleAddClick}>Add Expense</Button>
    </div>
    <ExpenseTable expenses={expenses} onEdit={handleEditClick} onDelete={deleteExpense} onToggleSame={toggleSame} />
    <Button variant="warning" onClick={restoreLastDeleted}>Undo Delete</Button>

    <Modal show={showModal} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>{editingIndex !== null ? 'Edit Expense' : 'Add Expense'}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Row>
            <Col>
              <Form.Group>
                <Form.Label>Expense</Form.Label>
                <Form.Control
                  type="text"
                  name="Expense"
                  placeholder="Expense"
                  value={currentExpense.Expense}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group>
                <Form.Label>Amount</Form.Label>
                <Form.Control
                  type="number"
                  name="Amount"
                  placeholder="Amount"
                  value={currentExpense.Amount}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group>
                <Form.Label>Paid</Form.Label>
                <Form.Control
                  type="number"
                  name="Paid"
                  placeholder="Paid"
                  value={currentExpense.Paid}
                  onChange={handleChange}
                  required
                  disabled={currentExpense.Same}
                />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group>
                <Form.Check
                  type="checkbox"
                  label="Same"
                  name="Same"
                  checked={currentExpense.Same}
                  onChange={handleChange}
                  className="add-expense-checkbox"
                />
              </Form.Group>
            </Col>
          </Row>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Cancel
        </Button>
        <Button variant="primary" onClick={handleSaveClick}>
          Save Changes
        </Button>
      </Modal.Footer>
    </Modal>
  </div>
);
};

export default App;
