import React from 'react';
import { Table, Button } from 'react-bootstrap';

const ExpenseTable = ({ expenses, onEdit, onDelete, onToggleSame }) => {
  const handleCheckboxChange = (index) => {
    onToggleSame(index);
  };

  return (
    <Table striped bordered hover>
      <thead>
        <tr>
          <th className="col-expense">Expense</th>
          <th className="col-amount">Amount</th>
          <th className="col-paid">Paid</th>
          <th className="col-same-paid">Same Paid</th> {/* Updated Column Heading */}
          <th className="col-difference">Difference</th>
          <th className="col-actions">Actions</th>
        </tr>
      </thead>
      <tbody>
        {expenses.map((expense, index) => (
          <tr key={index}>
            <td className="col-expense">{expense.Expense}</td>
            <td className="col-amount">{expense.Amount}</td>
            <td className="col-paid">{expense.Paid}</td>
            <td className="col-same-paid">
              <input
                type="checkbox"
                className="form-check-input"
                checked={expense.Same}
                onChange={() => handleCheckboxChange(index)}
              />
            </td>
            <td className="col-difference">{expense.Difference}</td>
            <td className="col-actions">
              <Button variant="primary" onClick={() => onEdit(index)}>Edit</Button>
              <Button variant="danger" onClick={() => onDelete(index)}>Delete</Button>
            </td>
          </tr>
        ))}
      </tbody>
    </Table>
  );
};

export default ExpenseTable;
